import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';

@IonicPage()

@Component({
  selector: 'page-aergm',
  templateUrl: 'aergm.html',
})

export class AergmPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private iab: InAppBrowser) {

    const browser = this.iab.create('http://localhost:5155/docs/Airport Emergency Response Grid Map of CSMIA WEF 02 JANUARY 2020.pdf');
    browser.show();
    this.navCtrl.setRoot('HomePage');
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AergmPage');
  }

}



